@import "../../App.css";

.Body{
    background-color: rgb(255, 255, 255);
    align-self: center;
    width:100%;




}
.Wrapper{
padding-top: 150px;
padding-bottom: 150px;
width: 70vw;

margin: 0 auto;

}

.ItemsCarouselWrapper{

    
    width: 90vw;

    padding-top: 30px;
    padding-bottom: 30px;
    margin: 0 auto;
    margin-top: -70px;

    
}
.CarouselCard{
    box-sizing: border-box;
   
    

    height: 25vh;

    margin-top:50px;
    margin-left: 50px;
    margin-right: 50px;

    background-color: var(--secondary);
    border-radius: 0.4rem;
    box-shadow:
  0.2px 0.5px 0.5px rgba(0, 0, 0, 0.02),
  0.5px 1.1px 1.3px rgba(0, 0, 0, 0.028),
  1px 2.1px 2.4px rgba(0, 0, 0, 0.035),
  1.8px 3.8px 4.2px rgba(0, 0, 0, 0.042),
  3.3px 7.1px 7.9px rgba(0, 0, 0, 0.05),
  8px 17px 19px rgba(0, 0, 0, 0.07)
;

}

.CarouselCardActive{
    
    
    height: 30vh;
    width:110%;
margin-top: 20px;
margin-bottom: 20px;

    margin-left: -5%;
    background-color: var(--secondary);
    border-radius: 1rem;
    box-shadow:
  0.2px 0.5px 0.5px rgba(0, 0, 0, 0.02),
  0.5px 1.1px 1.3px rgba(0, 0, 0, 0.028),
  1px 2.1px 2.4px rgba(0, 0, 0, 0.035),
  1.8px 3.8px 4.2px rgba(0, 0, 0, 0.042),
  3.3px 7.1px 7.9px rgba(0, 0, 0, 0.05),
  8px 17px 19px rgba(0, 0, 0, 0.07)
;
      
}

.mainImg {
    height:20vh;
    object-fit: cover;

    
}


.CarouselPhoto {
    color:var(--white);
    height:100%;
    display:flex;
    justify-content:center;
    align-items: center;

}


h1{
    font-size: 4.5em;
}


.AboutBackground{
    width: 100%;
    display:flex;
    background-color:#777;
    padding: 20px 0;
    justify-content:center;
;


}

.About{
    width:70vw;
    height:500px;


    color:var(--white);
    
    
}

.Blog{
   
}
.post h1 {
    font-size:2em;
}

.post{
    padding: 2em;
    margin-top: 2em;
    margin-bottom: 2em;
    box-shadow:
    0.2px 0.5px 0.5px rgba(0, 0, 0, 0.02),
    0.5px 1.1px 1.3px rgba(0, 0, 0, 0.028),
    1px 2.1px 2.4px rgba(0, 0, 0, 0.035),
    1.8px 3.8px 4.2px rgba(0, 0, 0, 0.042),
    3.3px 7.1px 7.9px rgba(0, 0, 0, 0.05),
    8px 17px 19px rgba(0, 0, 0, 0.07)
}

.photo {
    width: 100%;
}



















@media (max-width:900px){
    
h1{
    font-size: 4.5em;


}
    .Wrapper{
        padding-top: 20px;
        padding-bottom: 150px;
        width: 100vw;
        
        margin: 0 auto;
        
        }


    .ItemsCarouselWrapper svg{
        display:none;
    }

    .CarouselCard,.CarouselCardActive{
        background-color: var(--secondary);
        border-radius: 0.4rem;
        box-shadow: 0 10px 20px -15px rgba(15, 72, 179, 0.2);
       


    }
    .CarouselCard{
        height: 250px;
        margin-left: 10px;
        margin-right: 10px;
        margin-top: 10px;
        margin-bottom: 0px;
        
    }
    .CarouselCardActive{
        visibility: hidden;
        height: 300px;
        width:110%;
        margin-top:0px;
        margin-left: 0%;

        
    }
    


    .ItemsCarouselWrapper{
 
        width: 100vw;
        margin: 0 auto;
    }
}

